<template>
  <div id="JobLogs">
    <span class="heading">
      <div>Job Logs</div>
      <div>
        <button @click="loadJobLogs(-1,displayDate)"></button>
        {{getdisplayDate(displayDate)}}
        <button @click="loadJobLogs(1,displayDate)"></button>
      </div>
    </span>
    
    <span class="tablerowheader">
      <label>Job Name</label>
      <label>Start Time</label>
      <label>End Time</label>
      <label>Count</label>
      <label>Status</label>
    </span>
    <span v-for="(item, index) in joblog" :key="index">
      <span class="tablerow">
        <label>{{item.jobName}}</label>
        <label>{{item.startTime}}</label>
        <label>{{item.endTime}}</label>
        <label>{{item.count}}</label>
        <label
          :class="[ item.status === 'ERROR' ? 'error' : item.status === 'SUCCESS' ? 'success' : 'default']"
        >{{item.status}}</label>
      </span>
    </span>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: {},
  data() {
    return {
      displayDate: "",
      joblog: [
        /* 
        {
          jobName:
            "mr.mind.master.common.api.v5_0.fileupload::removeTemporaryPortfolio",
          startTime: "Mon Dec 17 2018 00:00:00 GMT+0000 (UTC)",
          endTime: "Mon Dec 17 2018 00:00:00 GMT+0000 (UTC)",
          status: "ERROR"
        },
        {
          jobName:
            "mr.mind.master.common.api.v5_0.fileupload::removeTemporaryPortfolio",
          startTime: "Mon Dec 17 2018 00:00:00 GMT+0000 (UTC)",
          endTime: "Mon Dec 17 2018 00:00:00 GMT+0000 (UTC)",
          status: "SUCCESS"
        },
        {
          jobName:
            "mr.mind.master.common.api.v5_0.fileupload::removeTemporaryPortfolio",
          startTime: "Mon Dec 17 2018 00:00:00 GMT+0000 (UTC)",
          endTime: "Mon Dec 17 2018 00:00:00 GMT+0000 (UTC)",
          status: "SCHEDULED"
        } */
      ]
    };
  },
  methods: {
    getdisplayDate: function(displayDate) {
      if (
        displayDate === "" ||
        displayDate === null ||
        displayDate === undefined
      ) {
        return "No Data";
      }
      return (
        displayDate.substr(8, 2) +
        ", " +
        [
          "Jan",
          "Feb",
          "Mar",
          "Apr",
          "May",
          "Jun",
          "Jul",
          "Aug",
          "Sep",
          "Oct",
          "Nov",
          "Dec"
        ][parseInt(displayDate.substr(5, 2)) - 1] +
        " " +
        displayDate.substr(0, 4)
      );
    },
    loadJobLogs: async function(offset, date) {
      await axios
        .get(
          process.env.VUE_APP_DEV +
            `joblogs.xsjs?offset=${offset}&date=${date}`,
          { withCredentials: true }
        )
        .then(response => {
          this.joblog = response.data.logs;
          this.displayDate = response.data.date.substr(0, 10);
        })
        .catch(e => alert(JSON.stringify(e.response.data)));
    }
  },
  created() {
    this.loadJobLogs(0, "");
  }
};
</script>

<style scoped>
@import "https://fonts.googleapis.com/css?family=Roboto|Jaldi|Imprima|IBM+Plex+Sans";

#JobLogs {
  padding-left: 20px;
  padding-right: 20px;
}

#JobLogs .heading {
  border-bottom: 2px solid;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  padding-left: 10px;
  padding-bottom: 10px;
  margin-top: 20px;
}

#JobLogs .heading div {
  font-size: 1.3em;
  font-family: Roboto, "IBM Plex Sans", Verdana, Tahoma;
  font-weight: bold;
  color: white;
  letter-spacing: 0.02em;
  text-align: left;
  color: gray;
  overflow: hidden;
  flex: 1;
}

#JobLogs .heading div:last-child {
  text-align: right;
}

#JobLogs .heading div button {
  outline: none;
  border-radius: 2px;
  height: 25px;
}

#JobLogs .tablerowheader {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-basis: 99%;
  margin-top: 25px;
  border-bottom: 1px solid;
  padding-bottom: 5px;
}

#JobLogs .tablerowheader label {
  flex: 1;
  font-size: 1.3em;
  font-family: Roboto, "IBM Plex Sans", Verdana, Tahoma;
  font-weight: bold;
  letter-spacing: 0.02em;
  color: gray;
  border-right: 1px solid;
}

#JobLogs .tablerowheader label:last-child {
  border-right: 1px transparent;
}

#JobLogs .tablerowheader label:last-child {
  border-right: 1px transparent;
}

#JobLogs .tablerowheader label:nth-child(1) {
  flex-basis: 30%;
}

#JobLogs .tablerow {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-basis: 99%;
  margin-top: 10px;
  border-bottom: 1px dotted;
  padding-bottom: 5px;
}

#JobLogs .tablerow label {
  flex: 1;
  font-size: 0.8em;
  font-family: Roboto, "IBM Plex Sans", Verdana, Tahoma;
  font-weight: normal;
  letter-spacing: 0.02em;

  border-right: 1px solid;
}

#JobLogs .tablerow label:last-child {
  border-right: 1px transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-weight: normal;
  color: aliceblue;
  font-size: 0.9em;
}

#JobLogs .tablerow label:last-child {
  border-right: 1px transparent;
}

#JobLogs .tablerow label:nth-child(1) {
  flex-basis: 30%;
  word-break: break-all;
  text-align: left;
}

#JobLogs .tablerow label:nth-child(4),
#JobLogs .tablerowheader label:nth-child(4) {
  word-break: break-all;
  max-width: 80px;
}

#JobLogs .tablerow label:nth-child(5),
#JobLogs .tablerowheader label:nth-child(5) {
  word-break: break-all;
  max-width: 100px;
}

#JobLogs .tablerow .success {
  background-color: green;
}

#JobLogs .tablerow .error {
  background-color: red;
}

#JobLogs .tablerow .default {
  background-color: gray;
}

@media screen and (max-width: 900px) {
  #JobLogs .heading,
  #JobLogs .tablerowheader,
  #JobLogs .tablerow {
    flex-direction: column;
  }

  #JobLogs .heading div {
    justify-content: center;
    align-items: center;
  }

  #JobLogs .tablerowheader label,
  #JobLogs .tablerow label {
    flex-basis: unset;
    border-right: 0px;
    border: 1px solid;
    border-radius: 2px;
    margin-top: 3px;
    font-size: 1em;
    word-break: break-all;
  }

  #JobLogs .tablerow label:nth-child(4),
  #JobLogs .tablerowheader label:nth-child(4),
  #JobLogs .tablerow label:nth-child(5),
  #JobLogs .tablerowheader label:nth-child(5) {
    word-break: unset;
    max-width: unset;
  }
}
</style>
