<template>
  <div id="maincontainer">
    <div class="header" :style="{ backgroundImage: `url(${ bg })`}">
      <!-- <div class="header"> -->
      <div class="menu" @click="openSidebar = !openSidebar">
        <img src="@/assets/sidebaropen.png" v-show="openSidebar">
        <img src="@/assets/sidebarclose.png" v-show="!openSidebar">
      </div>
    </div>
    <div class="contentcontainer">
      <div :class="{'contentsidebar':true, 'open': openSidebar}">
        <span v-for="(menuItem, index) in menu" :key="index">
          <span
            :class="{'sidemenu': true, 'menuselected': menuIndex === index}"
            @click="menuIndex = index, navigate(menuItem.id)"
          >
            <img :src="menuItem.img">
            <div>{{menuItem.name}}</div>
          </span>
        </span>
      </div>
      <div class="content">
        <router-view/>
      </div>
    </div>
    <div class="footer"></div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "LandingPage",
  components: {},
  data() {
    return {
      bg: require("@/assets/header.jpg"),
      openSidebar: false,
      menuIndex: 0,
      menu: [
        {
          img: require("@/assets/dashboard.png"),
          name: "Dashboard",
          id: "dashboard"
        },
        {
          img: require("@/assets/schedule.png"),
          name: "Job Logs",
          id: "joblogs"
        },
        {
          img: require("@/assets/layer.png"),
          name: "Hazard Layer",
          id: "hazardlayer"
        }
      ]
    };
  },
  methods: {
    navigate: function(navigateTo) {
      this.$router.push("/" + navigateTo);
    }
  },
  async mounted() {
    await axios
      .get(process.env.VUE_APP_DEV + "getToken.xsjs", { withCredentials: true })
      .then(response => response)
      .catch(e => alert(JSON.stringify(e)));
  }
};
</script>


