import Vue from "vue";
import Router from "vue-router";
import HazardLayer from "./components/HazardLayer.vue";
import JobLogs from "./components/JobLogs.vue";

Vue.use(Router);

const routes = [
  {
    path: "/hazardlayer",
    name: "HazardLayer",
    component: HazardLayer
  },
  {
    path: "/joblogs",
    name: "JobLogs",
    component: JobLogs
  }
];

export default new Router({
  mode: "abstract",
  base: process.env.BASE_URL,
  routes: routes
});
